body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card{
  margin-left: auto;
  margin-right: auto;
  margin-top: 1.7em;
  border-radius: 25px;
}
.carousel-inner{
height: 500px;
}
.time1{
margin-left: 30em;
font-size: 20px;
}
a{
  padding: 0 10px;
}
.carousel-item {
text-align: center;
}
a:hover{
  background-color: rgb(13, 168, 207);
}
label{
  margin-top: 0.8em;
  margin-bottom: .6em;
}
.container{
  margin-left: auto;
  margin-right: auto;
}
.modal {
  display: none;
  position: absolute;
  width: 100%; 
  height: 100%; 
  background-color: rgb(241, 155, 230); 
  background-color: rgba(187, 92, 179, 0.4);
}
.modal-content {
  margin: 15% auto; 
  padding: 20px;
  border: 1px solid rgba(0, 0, 0, 0);
  width: 20%; 
}
.close {
  color: rgb(255, 0, 0);
  padding-left: 15em;
  font-size: 28px;
  font-weight: bold;
}
.carousel-inner > .item > img, .carousel-inner > .item > a > img {
  width: 100%;
}
.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
#myBtn{
    display: block;
    width: 100px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: .5em;
}
.modal-dialog{
    width: 250px;
}
.footer{
  background-color: rgb(0, 0, 0);
}
#footer {
  position:absolute;
  bottom:0;
  width: 100%;
}

#currentTime{
border: none;
background-color: black;
color: rgb(228, 144, 34);
font-size: 30px;
text-align: center;
}
